@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
.user{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;grid-gap:10px;gap:10px}

.comment{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.commentTxt{background-color:#cfcfcf;border-radius:10px;padding:10px}

.commentList{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;grid-gap:20px;gap:20px}

.post{background-color:#fff;padding:10px;border-radius:10px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;grid-gap:30px;gap:30px}.postTxt{font-size:16px}

.postList{max-width:1000px;margin:40px auto;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;grid-gap:20px;gap:20px}

*{margin:0;padding:0}body{font-family:'Roboto';background-color:#363636}ul{list-style:none}

